.formArea {
  box-sizing: border-box;
  padding: 0 10px;
  font-family: 'Noto Sans', sans-serif;
  width: 100%;
  height: 35px;
  color: #a3a3a3;
  background-color: #252527;
  border: 1px solid #4a4a4a;
  outline: none;
  margin-top: 20px;
  position: relative;
  transition: all 0.4s ease-in-out 0s;
}

.messageArea {
  composes: formArea;
  height: 120px;
  padding: 10px;
  resize: none;
}

.formArea:focus {
  border: 1px solid transparent;
}

.errorMessage {
  color: #ff3e3e;
}





